.c-btnSlide {
  width: 100%;
  max-width: 12.5rem;
  background: #408F95;
  display: inline-block;
  position: relative;
}

.c-btnSlide::before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  border: 1px solid #408F95;
  position: absolute;
  top: 0.3125rem;
  right: -0.3125rem;
}

@media (hover: hover) {
  .c-btnSlide:hover {
    opacity: 1;
  }
  .c-btnSlide:hover .c-btnSlide__block::after {
    transform: scale(1, 1);
  }
  .c-btnSlide:hover span:first-child {
    color: #408F95;
  }
  .c-btnSlide:hover span:nth-child(2) {
    background: #408F95;
  }
}
.c-btnSlide__block {
  width: 100%;
  height: 100%;
  display: flex;
  gap: 0.5rem;
  padding: 1rem;
  justify-content: center;
  align-items: center;
  position: relative;
}

.c-btnSlide__block::after {
  background: #fff;
  position: absolute;
  top: 1px;
  left: 1px;
  content: "";
  width: calc(100% - 2px);
  height: calc(100% - 2px);
  transform: scale(0, 1);
  transform-origin: left top;
  transition: transform 0.3s;
  z-index: 2;
}

.c-btnSlide span {
  position: relative;
  z-index: 3;
}

.c-btnSlide span:first-child {
  display: inline-block;
  font-size: 0.875rem;
  color: #fff;
  font-family: "Gotu", sans-serif;
  transition: color 0.3s;
}

.c-btnSlide span:nth-child(2) {
  display: inline-block;
  position: relative;
  width: 2.5rem;
  height: 1px;
  background: #fff;
  transition: background 0.3s;
}

.c-btnSlide span:nth-child(2)::before {
  display: block;
  content: "";
  width: 0.625rem;
  height: 1px;
  background: inherit;
  position: absolute;
  top: 0;
  right: 0;
  transform-origin: top right;
  transform: rotate(45deg);
}

